.vacancy{
  padding-bottom: 5.44em;
  border-bottom: 1px solid #EFEFEF;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 30em) 25.4375em;
    justify-content: space-between;
  }
  &__content{
    max-width: 71.0625em;
    margin-top: 3em;
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    .m-badge{
      height: 1.9375em;
      padding: 0 .625em;
      font-weight: 700;
      border-radius: .1875em;
      margin-right: auto;
      span{
        font-size: 1.125em;
      }
    }
    .vacancy-city{
      margin-right: 5em;
    }
  }
  .text{
    margin-top: 2.625em;
  }
}

.vacancy-sites{
  display: flex;
  align-items: center;
  &__items{
    margin-left: .5em;
    display: flex;
    align-items: center;
  }
  .socials__item{
    width: 1.5em;
    @include lastM(0 .5em 0 0);
  }
}

.vacancy-city{
  display: flex;
  align-items: center;
  padding: 0 .625em;
  height: 1.875em;
  img{
    width: 1em;
    flex-shrink: 0;
    margin-right: .5em;
  }
}

.vacancy-form{
  position: sticky;
  top: 1em;
  border-radius: 0.1875em;
  background: #2A3C73;
  padding: 2.5em 1.94em;
  color: #fff;
  @include grid100gap(2em);
  .text28{
    text-align: center;
  }
  &__fields{
    margin-top: 2em;
    @include grid100gap(.625em);
  }
  textarea{
    height: 8.125em;
  }
  .file-input{
    &__inner{
      border-color: #fff;
      color: #fff;
      path{
        fill: #fff;
      }
    }
  }
  .file-field{
    &__list{
      margin-top: .25em;
      justify-content: center;
    }
    .uploaded-file{
      margin-right: 0;
    }
  }
}

.vacancies-collection{
  padding: 2.5em 0 7.25em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.vacancies-slider{
  margin-top: 2.81em;
}

.vacancy-card{
  padding: 1.875em 1.25em 2.5em;
  background-color: $gray3;
  border-radius: .1875em;
  &__inner{
    @include grid100gap(1.5em);
  }
  &__title{
    min-height: 2.8125em;
    @include truncate(2);
  }
  &__desc{
    @include truncate(4);
    min-height: 4.75em;
  }
  &__salary{
    color: #159364;
  }
  .vacancy-city{
    display: inline-flex;
    background-color: #fff;
    margin-right: auto;
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.vacancy-main{
  .main{
    &__inner{
      flex-direction: row;
      justify-content: space-between;
    }
  }
}

.vacancy-ceo{
  display: flex;
  align-items: flex-start;
  &__quote{
    max-width: 31.02em;
    display: flex;
    align-items: flex-start;
    border-radius: 0.1875em;
    background: #2A2C2F;
    padding: 1.25em;
    img{
      width: 1.9em;
      flex-shrink: 0;
      margin-right: 1.13em;
    }
  }
  &__item{
    margin-left: 4.44em;
  }
  &__img{
    width: 10.125em;
    height: 10.125em;
    border-radius: 50%;
    display: block;
    object-fit: cover;
  }
  &__text{
    margin-top: 1.69em;
    text-align: center;
    @include grid100gap(.56em);
  }
}

.vacancy-welcome{
  padding: 4.44em 0 5em;
  color: #fff;
  &__inner{
    padding: 12.5em 1em;
    background-size: cover;
    background-position: center;
    position: relative;
    backdrop-filter: blur(2px);
    &:before{
      content: '';
      position: absolute;
      background: radial-gradient(81.2% 81.2% at 50% 45.6%, rgba(31, 33, 35, 0.50) 0%, rgba(31, 33, 35, 0.00) 100%);
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__content{
    position: relative;
    z-index: 2;
    text-align: center;
    max-width: 39em;
    margin: 0 auto;
  }
  &__nav{
    margin-top: 3em;
    display: flex;
    justify-content: center;
  }
}

.why{
  padding: 4.625em 0 5.5em;
  background-color: $gray3;
  &__items{
    margin-top: 3.5em;
    display: grid;
    grid-template-columns: repeat(6,calc(100% / 6 - 3.75em * 5 / 6));
    gap: 3.75em;
  }
}

.why-card{
  &__icon{
    height: 3.75em;
    width: auto;
    display: block;
    margin-bottom: 2em;
  }
}

.team{
  padding: 2.5em 0 5em;
  &__grid{
    margin-top: 3.3125em;
    display: grid;
    grid-template-columns: repeat(6,calc(100% / 6 - 1.875em * 5 / 6));
    gap: 1.875em;
    grid-auto-flow: dense;
  }
}

.team-img{
  &._big{
    grid-column: span 2;
    grid-row: span 2;
  }
  &__wrap{
    @include imgCover(100%);
  }
}

.club{
  padding: 5em 0 6.25em;
  background-color: $black;
  color: #fff;
  &._light{
    background-color: transparent;
    color: $black;
    padding: 1.5em 0;
    .club-socials__item,
    .club-form{
      background-color: $gray3;
    }
  }
  .text35{
    text-align: center;
  }
}

.club-form{
  padding: 3.75em 2em;
  border-radius: 0.0625em;
  background: #2A2D30;
  margin-top: 3.75em;
  display: flex;
  align-items: center;
  &__title{
    white-space: nowrap;
    flex-shrink: 0;
  }
  .fg{
    flex-grow: 1;
    margin: 0 1.875em;
  }
  &__btn{
    flex-shrink: 0;
    width: 23.375em;
  }
}

.club-socials{
  margin-top: 1.875em;
  &__item{
    padding: 2.81em 1em 2.81em 2.5em;
    display: flex;
    align-items: center;
    border-radius: 0.0625em;
    background: #2A2D30;
    font-weight: 700;
    &:hover{
      background: darken(#2A2D30,10%);
    }
    img{
      width: 5em;
      flex-shrink: 0;
      margin-right: 2.19em;
    }
    span{
      font-size: 1.125em;
      line-height: 120%;
      text-decoration: underline;
    }
  }
}
