.shops-map{
  padding-top: 5em;
}

.shop-item{
  padding-bottom: 4.88em;
  &__grid{
    margin-top: 2.875em;
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__link{
    margin-left: 1em;
    img{
      width: 1.875em;
      margin-right: .75em;
      flex-shrink: 0;
    }
  }
  .text{
    margin-top: 1.5em;
    @include grid100gap(1.5em);
    h3{
      margin-top: .3em;
    }
    h3,
    p{
      margin-bottom: 0;
    }
    hr{
      height: .5em;
    }
  }
}

.shops-page{
  padding-bottom: 7em;
  border-bottom: 1px solid #EFEFEF;
  .shops__grid{
    margin-top: 2.44em;
  }
  &__text{
    margin-top: 2.5em;
  }
}
