.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    padding: 1em 0;
    text-align: center;
    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog{
        text-align: left;
        vertical-align: middle;
    }

    &__bg {
        background: rgba($black, 0.80);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;

        .modal__bg {
            display: block;
        }
    }

    &__dialog {
        border-radius: .1875em;
        background: #fff;
        width: 31.25em;
        max-width: calc(100% - 1em);
        position: relative;
        z-index: 3;
        padding: 1.875em 3.125em;
        display: inline-block;
    }
    &__close {
        z-index: 10;
        border-top: 0;
        background-color: transparent;
        width: 1.5em;
        display: flex;
        align-items: center;
        position: absolute;
        top: .81em;
        right: .69em;
        svg {
            width: 100%;
            height: auto;
        }
        &:hover{
            path{
                stroke: $black;
            }
        }
    }
    &__top{
        text-align: center;
        margin-bottom: 2em;
        position: relative;
    }
    &__back{
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
        width: 1.5em;
        @include center;
        svg{
            width: 100%;
            height: auto;
        }
        &:hover{
            path{
                stroke: $primary;
            }
        }
    }
    .buyer__tabs{
        justify-content: space-between;
        margin-bottom: 2em;
    }
}

.modal-form{
    @include grid100gap(2em);
    &__fields{
        @include grid100gap(.5em);
    }
    &__hint{
        text-align: center;
    }
    &__alert{
        text-align: center;
        margin: 0 0 .5em;
        padding: .625em 1.56em;
        color: #302A73;
        background-color: #D6D2FF;
        border-radius: .1875em;
    }
}

.review-field{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 3.25em;
    padding: 0 1.56em;
    background-color: $gray3;
    border-radius: .1875em;
}

.password-modal{
    .modal-form__fields{
        gap: 1.5em;
    }
}

.login-buttons,
.socials-login{
    @include center;
}

.login-buttons{
    .line-btn{
        @include lastM(0 2em 0 0);
    }
}

.product-modal{
    .modal__dialog{
        width: 95.6875em;
        padding: 3.75em 3.125em;
    }
    &__grid{
        display: grid;
        grid-template-columns: calc(100% - 34.8125em - 1.875em) 34.8125em;
        gap: 1.875em;
    }
    .catalog-info{
        margin-top: 2.5em;
        gap: 2.5em;
    }
    .gallery{
        height: 38.25em;
        &__small{
            .swiper-slide{
                height: calc(100% / 5 + .625em / 10);
            }
        }
        &__big{
            .gallery__img{
                height: 38.25em;
            }
        }
    }
}

.compare-modal,
.product-added-modal{
    .modal__dialog{
        width: 64.4375em;
    }
    .modal__top{
        text-align: left;
    }
    @media screen and (min-width: 993px){
        .product-card__nav{
            grid-template-columns: 100%;
            gap: .5em;
        }
    }
}

.product-added-modal{
    .cart-table__rows{
        margin: 0;
    }
    .cart-item{
        grid-template-columns: 100%;
    }
    &__top{
        margin-top: 2.5em;
        display: grid;
        grid-template-columns: calc(100% - 19.25em) 17.25em;
        gap: 2em;
        .m-btn{
            @include lastM(0 0 1.25em);
            width: 100%;
        }
    }
    .collection{
        margin-top: 2.5em;
        padding: 0;
        &__content{
            margin-top: 2em;
        }
        @media screen and (min-width: 993px){
            .text24{
                line-height: 167%;
            }
            .swiper{
                &-container{
                    width: calc(100% + 1em);
                }
                &-slide{
                    width: calc(100% / 3);
                    padding-right: 1em;
                }
                &-nav{
                    position: absolute;
                    bottom: 100%;
                    right: 0;
                    margin-bottom: 2em;
                    display: flex;
                    align-items: center;
                }
                &-button{
                    position: static;
                    transform: unset;
                    margin: 0 0 0 .625em;
                }
                &-pagination{
                    display: none;
                }
            }
        }
    }
}

.compare-modal{
    &__grid{
        display: grid;
        grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
        gap: 1em;
    }
    &__bottom{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 2em;
    }
    &__btn{
        position: relative;
        .pin{
            font-size: 1em;
        }
    }
}

.pin{
    position: absolute;
    top: -.5em;
    right: -.5em;
    padding: 0 .125em;
    min-width: .9375em;
    height: .9375em;
    border-radius: 0.625em;
    background-color: $primary;
    color: #fff;
    @include center;
    span{
        font-size: .625em;
        line-height: 110%;
    }
}

.change-modal{
    .modal__dialog{
        width: 95.6875em;
        padding: 2.5em 3.125em;
    }
    &__top{
        display: flex;
        .search-field{
            flex-grow: 1;
        }
        .m-select{
            select{
                height: 3.57em;
                padding-left: 1.71em;
                padding-right: 3em;
                background-position: right 1.71em center;
            }
        }
    }
    &__sort{
        display: flex;
        align-items: center;
        flex-shrink: 0;
        margin-left: 1.875em;
        .m-select{
            margin-left: 1em;
        }
    }
    &__grid{
        display: grid;
        grid-template-columns: repeat(2,calc(50% - 1.25em));
        gap: 2.5em;
        margin-top: 2.5em;
    }
}

.category-select{
    margin-left: 1.875em;
    flex-shrink: 0;
    position: relative;
    &__title{
        pointer-events: none;
        position: absolute;
        top: 50%;
        left: 1.5em;
        transform: translateY(-50%);
    }
    .m-select{
        select{
            padding-left: 7.25em;
        }
    }
}

.change-card{
    padding: .625em 1.875em .625em .625em;
    display: grid;
    grid-template-columns: 12.9375em calc(100% - 12.9375em - 1.875em);
    gap: 1.875em;
    .product-card{
        &__img{
            padding-bottom: 87.4%;
        }
        &__prices{
            margin-top: 0;
        }
    }
    &__content{
        display: flex;
        flex-direction: column;
    }
    &__bottom{
        margin-top: auto;
        padding-top: 1.5em;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .add-btn{
        width: 13.3125em;
        flex-shrink: 0;
    }
}


@media screen and (max-width: 992px){
    .modal{
        &__dialog{
            gap: 1.25em;
        }
        &__top{
            gap: .625em;
            .text33{
                font-size: 1.875em;
            }
        }
        &__desc{
            font-size: 1.0625em;
            line-height: 141%;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        padding-top: .5em;
        &:after{
            display: none;
        }
        &__dialog{
            padding: 2em .94em 1.25em;
            margin: 0 auto;
        }
    }
}
