select{
  font-family: $font1;
}

.fg {
  position: relative;
  margin: 0;
  textarea,
  input {
    border: 1px solid #E6E6E6;
    padding: 0 1.56em;
    height: 3.125em;
    width: 100%;
    font-size: 1em;
    line-height: 133%;
    border-radius: 0.1875em;
    background-color: #fff;
    color: $black;
    &::placeholder {
      color: $gray;
    }
    &:focus {
      border-color: $black;
    }
  }
  textarea{
    height: 6.875em;
    padding-top: .94em;
    padding-bottom: .94em;
    resize: none;
  }
  &._error{
    textarea,
    input{
      border-color: $primary;
    }
  }
  &__hint{
    margin-top: .6em;
  }
}

.form-checkbox,
.add-btn,
.color-check,
.color-item,
.m-radio,
.file-input,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: block;
    padding-left: 1.75em;
    min-height: 1.125em;
    span{
      font-size: .875em;
      line-height: 128.5%;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      transition: all .100s;
      background-color: #fff;
      border: 1px solid #D7D7D7;
      width: 1.125em;
      height: 1.125em;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: $primary;
        border-color: $primary;
        background-image: url(../img/check.svg);
      }
    }
  }
  label {
    &:before {
      border-radius: .125em;
      @include bgImgSize(.625em);
    }
  }
}

.m-radio{
  input:checked~label{
    font-weight: 700;
    &:before{
      border-color: $primary;
    }
    &:after{
      opacity: 1;
    }
  }
  label{
    &:before{
      border-radius: 50%;
    }
    &:after{
      content: '';
      position: absolute;
      left: .25em;
      top: .25em;
      width: .625em;
      height: .625em;
      border-radius: 50%;
      background-color: $primary;
      transition: all .1s;
      opacity: 0;
      z-index: 2;
    }
    span{
      font-size: 1em;
      line-height: 118%;
    }
  }
}

.file-field{
  &__hint{
    margin-top: .625em;
    text-align: center;
  }
  &__list{
    display: flex;
    flex-wrap: wrap;
    margin-top: -.375em;
    width: calc(100% ) .375em;
  }
}

.file-input{
  input:hover~.file-input__inner{
    color: #fff;
    background-color: $black;
    path{
      fill: #fff;
    }
  }
  &__inner{
    svg{
      width: 1.1875em;
      height: auto;
      margin-right: .75em;
      flex-shrink: 0;
    }
  }
}

.uploaded-file{
  padding: 0 .625em 0 1.25em;
  height: 1.8125em;
  display: flex;
  align-items: center;
  border-radius: 0.1875em;
  background: #4D65B1;
  color: #fff;
  margin: .375em .375em 0 0;
  &__delete{
    width: 1.0625em;
    flex-shrink: 0;
    margin-left: .75em;
    svg{
      width: 100%;
      height: auto;
    }
  }
  span{
    font-size: 1em;
    line-height: 120%;
  }
}
