.favourite{
  margin-top: 2.56em;
  display: grid;
  grid-template-columns: repeat(4,calc(25% - 1.875em * 3 / 4));
  gap: 1.875em;
}

.favourite-nav{
  margin-top: 3.81em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__selects{
    display: flex;
  }
  .m-select{
    @include lastM(0 .56em 0 0);
  }
}

.m-select{
  select{
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    &::-ms-expand {
      display: none; // IE
    }

    padding: 0 3em 0 1.14em;
    height: 2.36em;
    border-radius: 0.21em;
    border: 1px solid $black;
    font-size: .875em;
    font-weight: 700;
    background-image: url(../img/select-arrow.svg);
    background-repeat: no-repeat;
    background-position: right 1.14em center;
    background-size: 1.14em auto;
  }
}
