@import "../vars.scss";
.breadcrumbs {
    padding: 1.56em 0;
    margin-bottom:  1.69em;
    &__item {
        position: relative;
        display: inline;
        span{
            font-size: .9375em;
            line-height: 133%;
        }
        &:last-child {
            color: $gray;
        }
        &:not(:first-child) {
            &:before {
                content: '/';
                margin: 0 .5em 0 .625em;
                font-size: .9375em;
                line-height: 133%;
                color: $gray;
            }
        }
    }
}



