.catalog-item{
  padding-bottom: 3.125em;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 34.5625em - 4.44em) 34.5625em;
    justify-content: space-between;
  }
  &__top{
    align-items: center;
  }
  &__nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    .favourite-btn,
    .compare-btn{
      margin-left: 1.81em;
      &__icon{
        width: 1.75em;
      }
      &:not(._added):hover{
        path{
          stroke: $primary;
        }
      }
    }
    .share-block{
      margin-left: 1.81em;
    }
  }
  &__action,
  &__rating{
    display: flex;
    align-items: center;
  }
  &__bottom{
    margin-top: 2.81em;
  }
}

.share-block{
  position: relative;
  z-index: 4;
  &._active{
    .share-block__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__toggle{
    width: 1.75em;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
  &__dropdown{
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    position: absolute;
    border-radius: 0.1875em;
    background: #FFF;
    padding: 1.875em;
    top: 100%;
    right: 0;
    margin-top: 1.5em;
    box-shadow: $boxShadow;
  }
  &__item{
    display: flex;
    align-items: center;
    font-weight: 700;
    color: $black;
    white-space: nowrap;
    text-align: left;
    &:not(:last-child){
      padding-bottom: 1.5em;
      margin-bottom: 1.5em;
      border-bottom: 1px solid #EDEDED;
    }
    &:hover{
      color: $primary;
    }
    img{
      width: 1.5em;
      flex-shrink: 0;
      margin-right: 1em;
    }
    span{
      font-size: 1em;
      line-height: 120%;
    }
  }
}

.gallery{
  position: relative;
  height: 51.375em;
  display: grid;
  grid-template-columns: 8.375em calc(100% - 9em);
  gap: .625em;
  align-items: center;
  &__img{
    display: block;
    position: relative;
    img{
      transition: all .3s;
      @include centerAbsolute;
      max-width: 100%;
      max-height: 100%;
      object-fit: contain;
    }
  }
  &__small{
    width: 100%;
    height: calc(100% - 6.25em);
    position: relative;
    .swiper{
      &-slide{
        height: calc(100% / 7 + .625em / 14);
        padding-bottom: .625em;
        position: relative;
        &:hover{
          cursor: pointer;
        }
        &:hover,
        &-thumb-active{
          .gallery__img{
            border-color: $black;
          }
        }
      }
    }
    .gallery__img{
      padding: 0;
      height: 100%;
      border: 1px solid #ECECEC;
      background: #F6F7F8;
    }
  }
  &__big{
    height: 100%;
    width: 100%;
    position: relative;
    .gallery__img{
      padding: 0;
      height: 51.375em;
      background-color: $gray3;
    }
  }
  .swiper{
    &-button{
      position: absolute;
      width: 1.5625em;
      z-index: 3;
      left: calc(8.375em / 2);
      transform: translateX(-50%);
      top: unset;
      border: none;
      margin: 0;
      background-color: transparent;
      svg{
        width: 100%;
        height: auto;
      }
      &-prev{
        top: 0;
      }
      &-next{
        bottom: 0;
      }
      &:hover{
        background-color: transparent;
        path{
          stroke: $black;
          opacity: 1;
        }
      }
    }
  }
  .product-card__badges{
    z-index: 3;
    top: 1.375em;
    left: 1.5625em;
  }
  &__logo{
    position: absolute;
    top: 1.5em;
    right: 1.19em;
    width: 7.1875em;
    z-index: 3;
    img{
      width: 100%;
      display: block;
    }
  }
}

.catalog-info{
  @include grid100gap(3em);
  &__nums,
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__colors{
    display: flex;
    .color-check{
      margin-right: .625em;
      &__bg{
        width: 1.875em;
        height: 1.875em;
        margin: 0;
      }
    }
  }
  &__prices{
    display: flex;
    align-items: center;
  }
  &__price{
    font-size: 2.25em;
    line-height: 119%;
    font-weight: 700;
  }
  &__price2{
    font-size: 1em;
    line-height: 120%;
    color: $gray;
    text-decoration: line-through;
    margin-left: 1.38em;
  }
  &__nav{
    display: flex;
    .cartcalc{
      flex-shrink: 0;
    }
  }
  &__buttons{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5625em));
    gap: 1.125em;
    margin-left: 1.125em;
    flex-grow: 1;
    .m-btn{
      padding: 0;
      span{
        font-size: .9375em;
      }
    }
  }
  &__text-wrap{
    @include grid100gap(1.5em);
  }
}

.catalog-delivery{
  &__item{
    &:not(:last-child){
      margin-bottom: .81em;
      padding-bottom: .81em;
      border-bottom: 1px solid #EEE;
    }
  }
  &__link{
    display: flex;
    align-items: center;
    justify-content: space-between;
    svg{
      width: 1em;
      height: auto;
      margin-left: 1em;
    }
    span{
      font-size: 1em;
      line-height: 120%;
    }
    &:hover{
      color: $primary;
      path{
        stroke: $primary;
      }
    }
  }
}

.catalog-consult{
  padding: 1.875em 1.25em;
  border-radius: 0.1875em;
  background: #D6D2FF;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__img{
    width: 6.8125em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  .socials{
    flex-direction: column;
    &__item{
      @include lastM(0 0 .625em);
    }
  }
  &__content{
    max-width: 17.6875em;
    @include grid100gap(1.06em)
  }
  &__desc{
    max-width: 16em;
  }
}

.props{
  padding: 3.125em 0;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 5em));
    gap: 0 10em;
    margin-top: 2.5em;
  }
  @media screen and (min-width: 993px){
    .prop-item{
      &:nth-last-child(2):nth-child(odd){
        border-bottom: none;
      }
    }
  }
}

.video-overview{
  padding: 3.125em 0 2.9em;
  .video{
    margin-top: 2.81em;
  }
}

.video{
  display: block;
  position: relative;
  &__img{
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(0deg, rgba(31, 33, 35, 0.50) 0%, rgba(31, 33, 35, 0.50) 100%);
      z-index: 2;
    }
    img{
      width: 100%;
      display: block;
    }
  }
  &__play{
    width: 8.25em;
    height: 8.25em;
    padding: 0;
    border-radius: 50%;
    @include centerAbsolute;
    z-index: 3;
    svg{
      width: 1.875em;
      height: auto;
    }
  }
}

.additional{
  padding: 2.9em 0;
}

.product-card2{
  &__nav{
    margin-top: 2em;
    @include grid100gap(1.0625em);
  }
}

.add-btn{
  font-weight: 700;
  &__inner{
    @include center;
    height: 3.125em;
    border-radius: .1875em;
    border: 1px solid $black;
  }
  &__rect{
    border: 1px solid #D7D7D7;
    width: 1.125em;
    height: 1.125em;
    flex-shrink: 0;
    margin-right: 1.25em;
    border-radius: .125em;
    background-image: url(../img/check.svg);
    @include bgImgSize(0);
  }
  &__title{
    font-size: .9375em;
    line-height: 133%;
  }
  ._view2{
    display: none;
  }
  input:checked,
  input:hover{
    &+.add-btn__inner{
      background-color: $black;
      color: #fff;
    }
  }
  input:checked{
    &+.add-btn__inner{
      .add-btn__rect{
        border-color: transparent;
        background-size: .625em auto;
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}

.additional-summary{
  mask-type: 2.94em;
  padding: 2.5em;
  background-color: $gray3;
  &__nav,
  &__top{
    @include center;
  }
  &__prices{
    display: flex;
    align-items: center;
    margin-left: 1.75em;
  }
  &__price2{
    margin-left: 1.06em;
    text-decoration: line-through;
  }
  &__nav{
    margin-top: 1.625em;
  }
}
