.order{
  .cart__sidebar{
    padding-top: 4.25em;
  }
}

.buyer{
  margin-top: 3.125em;
  @include grid100gap(1.5em);
  &__tabs{
    display: flex;
    align-items: center;
    .m-radio{
      @include lastM(0 1.875em 0 0);
    }
  }
  &__variant{
    display: none;
    &._active{
      display: block;
    }
  }
}

.delivery-form{
  margin-top: 1em;
  @include grid100gap(1.5em);
}

.delivery-place{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .375em));
  gap: .75em;
  padding: .375em 0;
  &__selected{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: .625em 1.56em .625em 1.38em;
    background: #E8E8E8;
  }
}

.order-success{
  margin-top: 2.81em;
  max-width: 49.1875em;
  &__info{
    @include grid100gap(1.5em);
    .text16{
      line-height: 162%;
      a{
        text-decoration: underline;
        font-weight: 700;
      }
    }
  }
  &__pay{
    margin-top: 3.125em;
  }
  &__props{
    margin-top: 1.69em;
    @include grid100gap(1em);
  }
  &__prop{
    .text16{
      @include lastM(0 .5em 0 0);
    }
  }
  &__nav{
    display: flex;
    margin: 1.5em 0;
    .m-btn{
      padding: 0 2em;
    }
  }
}

.orders{
  margin-top: 3em;
  @include grid100gap(1em);
}

.order-card{
  @include grid100gap(1.5em);
  padding: 1.875em;
  background-color: $gray3;
  &._disabled{
    color: $gray;
  }
  &__bottom,
  &__top{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__status{
    flex-shrink: 0;
    margin-left: 1em;
    padding: .875em 1.375em;
    border-radius: 0.1875em;
    background-color: #fff;
    display: flex;
    align-items: center;
    .text16{
      @include lastM(0 .5em 0 0);
    }
  }
  &__bottom{
    padding-top: .625em;
  }
  &__link{
    padding: 0 2em;
  }
  &__action{
    display: flex;
    align-items: center;
  }
  &__btn{
    display: flex;
    align-items: center;
    @include lastM(0 2em 0 0);
    svg{
      width: 1.125em;
      height: auto;
      flex-shrink: 0;
      margin-right: .5em;
    }
    span{
      font-size: .9375em;
      line-height: 133%;
      text-decoration: underline;
      transition: inherit;
    }
    &:hover{
      color: $primary;
      path{
        stroke: $primary;
      }
    }
  }
  &__info{
    @include grid100gap(1em);
  }
  &__item{
    display: flex;
    align-items: center;
    &-title,
    &-desc,
    &-account{
      font-size: 1em;
      line-height: 119%;
    }
    &-desc{
      font-weight: 700;
      margin-left: .5em;
    }
    &-account,
    &-btn{
      margin-left: .5em;
    }
    &-account{
      text-decoration: underline;
    }
    .m-badge{
      padding: 0 .625em;
      height: 1.6875em;
      margin-left: .5em;
      border-radius: .125em;
      span{
        font-size: .875em;
      }
    }
  }
}

.pagination{
  margin-top: 2.875em;
  &__btn{
    width: 100%;
  }
  &__items{
    margin-top: 2.06em;
    @include center;
  }
  &__item,
  &__dots{
    height: 2.3125em;
    margin: 0 .56em;
    font-weight: 500;
    @include center;
    span{
      font-size: .875em;
      line-height: 120%;
    }
  }
  &__item{
    border-radius: 3.125em;
    border: 1px solid #D9D9D9;
    min-width: 2.3125em;
    padding: 0 .5em;
    &._active,
    &:hover{
      background-color: $black;
      color: #fff;
      path{
        stroke: #fff;
      }
    }
    &._btn{
      padding: 0 1em;
      svg{
        width: 1.0625em;
        height: auto;
        &:first-child{
          margin-right: .625em;
        }
        &:last-child{
          margin-left: .625em;
        }
      }
    }
  }
}

.order-page{
  margin-top: 3.3125em;
  display: grid;
  grid-template-columns: calc(100% - 25.4375em - 1.875em) 25.4375em;
  gap: 1.875em;
  .order-card__status{
    margin: 0 1em 0 0;
  }
}

.order-detail{
  margin-top: 3.37em;
  .cart-table{
    margin-top: 2.62em;
  }
  .cart-table__grid{
    grid-template-columns: calc(100% - 44.5em) 5.625em 6.125em 5.625em 8.125em;
  }
}
