.faq{
  padding-bottom: 5em;
  &__content,
  .tabs{
    margin-top: 3.375em;
  }
  &__items{
    @include grid100gap(.5em);
  }
}

.tabs{
  display: flex;
  .m-link{
    cursor: pointer;
    transition: all .3s;
    &._active{
      color: $primary;
      text-decoration: underline;
    }
  }
}

.qa-card{
  border-radius: 0.25em;
  background-color: $gray3;
  &__show{
    cursor: pointer;
    padding: 1.875em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__btn{
    width: 1.5em;
    flex-shrink: 0;
    margin-left: 1em;
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__hidden{
    padding: 0 1.875em 1.875em;
    margin-top: -.375em;
    display: none;
  }
  &._active{
    background-color: $blue3;
    color: #fff;
    .qa-card__btn{
      svg{
        transform: rotate(-180deg);
      }
      path{
        stroke: #fff;
      }
    }
  }
}
