.main-promo{
  .main__img{
    opacity: .4;
  }
}

.catalog{
  padding: 6em 0 4.5em;
  &__top{
    margin-bottom: 3.94em;
  }
  &__inner{
    display: grid;
    grid-template-columns: 21.875em calc(100% - 21.875em - 5.44em);
    justify-content: space-between;
  }
  .m-check{
    label{
      span{
        font-size: 1em;
        line-height: 119%;
      }
    }
  }
}

.catalog-checkboxes{
  max-width: 15.25em;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .625em));
  gap: 1.25em;
}

.filter{
  margin-top: 2.06em;
  padding: 1.25em;
  border-radius: 0.1875em;
  background: #FCFCFC;
  &__buttons{
    margin-top: 2em;
    @include grid100gap(2em);
  }
}

.filter-group {
  &:not(:last-child) {
    padding-bottom: 2em;
    margin-bottom: 2em;
    border-bottom: 1px solid #EEE;
  }

  &:not(._active) {
    .filter-group__toggle {
      svg {
        transform: rotate(-180deg);
      }
    }
  }
  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__toggle {
    width: 1.5em;
    position: relative;
    @include center;
    &:hover {
      path {
        stroke: $black;
      }
    }
    &:before {
      content: '';
      @include centerAbsolute;
      width: 150%;
      height: 150%;
    }
    svg {
      width: 100%;
      height: auto;
    }
  }
  &__bottom {
    padding: 1.25em 0 0;
    display: none;
  }
  &__search {
    margin-bottom: 1.25em;
  }
  &__checks {
    @include grid100gap(.75em);

    &._scroll {
      overflow-y: auto;
      max-height: 12.8125em;

      &::-webkit-scrollbar {
        width: 0.1875em;
        height: 0.1875em;
      }

      &::-webkit-scrollbar-track {
        background: #E6E6E6;
        border-radius: 0.625em;
      }

      &::-webkit-scrollbar-thumb {
        background: $gray;
        border-radius: 0.625em;
      }
    }
  }
  .m-check {
    input:checked ~ label {
      font-weight: 700;

      span {
        text-decoration: underline;
      }
    }
  }
  &__checks._cut {
    @include grid100gap(.94em);

    &:not(._show-all) {
      .m-check:nth-child(7) ~ .m-check {
        .color-check:nth-child(5) ~ .color-check {
          display: none;
        }
      }

      &._show-all {
        .more-checkbox__wrap {
          display: none;
        }
      }
    }
  }
}

.more-checkbox{
  line-height: 133%;
  &__wrap{
    margin-top: .5em;
    display: flex;
  }
}

.filter-slider{
  &__item{
    min-height: .9375em;
    padding: 0 .125em;
    display: flex;
    align-items: center;
  }
  .range-slider{
    width: 100%;
    background-color: #D2D2D2;
    border: none;
    box-shadow: none;
    height: .125em;
    border-radius: .125em;
    .noUi-connect{
      border-radius: .125em;
      background-color: $primary;
    }
    .noUi-handle{
      border: none;
      box-shadow: none;
      background-color: $primary;
      width: .625em;
      height: .625em;
      top: -.25em !important;
      right: -.25em !important;
      border-radius: 50%;
      cursor: pointer;
      &:before{
        opacity: 0;
        width: 300%;
        height: 100%;
        @include centerAbsolute;
      }
      &:after{
        display: none;
      }
    }
  }
  &__outputs{
    margin-bottom: 1.25em;
    display: flex;
    justify-content: space-between;
    @include sameNumWidth;
    &-item{
      padding: 0 .69em;
      height: 2.4375em;
      display: flex;
      align-items: center;
      border-radius: 3.125em;
      background: #F5F5F5;
    }
    &-title,
    &-desc,
    &-currency{
      font-size: .9375em;
      line-height: 140%;
    }
    &-title{
      color: $gray;
      margin-right: .55em;
    }
    &-desc{
      font-weight: 700;
    }
    &-currency{
      font-weight: 700;
      margin-left: .25em;
    }
  }
}

.color-check{
  input:checked+label{
    .color-check__bg{
      outline: 2px solid $black !important;
      &._white{
        border-color: #fff;
      }
      &._black{
        outline-color: $primary !important;
      }
    }
  }
  label{
    display: flex;
    align-items: center;
  }
  &__bg{
    width: 1.625em;
    height: 1.625em;
    flex-shrink: 0;
    margin-right: 1.06em;
    &._white{
      border: 1px solid #F0F0F0;
    }
  }
  &__title{
    font-size: 1em;
    line-height: 120%;
  }
}

.selected-filters{
  display: flex;
  margin-top: 1.375em;
  flex-wrap: wrap;
  width: calc(100% + .5em);
}

.filter-clear,
.selected-filter{
  margin: .5em .5em 0 0;
  border-radius: 0.1875em;
  height: 2.0625em;
  padding: 0 1em;
  display: flex;
  align-items: center;
}

.selected-filter{
  background-color: $gray3;
  &__title,
  &__desc{
    font-size: 0.875em;
    line-height: 120%;
  }
  &__desc{
    font-weight: 700;
    margin-left: .4em;
  }
  &__remove{
    width: 1em;
    height: auto;
    @include center;
    margin-left: .625em;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
}

.filter-clear{
  background-color: #E6E6E6;
  color: $black;
  &:hover{
    color: #fff;
    background: $primary;
    path{
      stroke: #fff;
    }
  }
  span{
    font-size: .875em;
    line-height: 120%;
  }
  svg{
    width: 1em;
    height: auto;
    margin-left: .625em;
  }
}

.catalog-items{
  margin-top: 1.81em;
}

.consult{
  border-radius: 0.1875em;
  background: #D6D2FF;
  color: #302A73;
  padding: 2.5em .625em;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  &__top{
    width: 100%;
    text-align: center;
  }
  &__title{
    font-size: 1.625em;
    line-height: 119%;
    font-weight: 700;
  }
  &__desc{
    margin: .94em auto 0;
    max-width: 15.125em;
  }
  &__img{
    width: 100%;
    display: block;
    margin: 2em 0;
  }
  &__bottom{
    text-align: center;
  }
  .socials{
    justify-content: center;
    margin-top: 1.625em;
  }
}

.catalog-collection{
  padding: 4.88em 0;
  border-bottom: 1px solid #EFEFEF;
}

.catalog-collection2{
  padding: 4.13em 0;
}

.info{
  padding: 5.625em 0;
  overflow: hidden;
  &__grid{
    display: grid;
    grid-template-columns: 48.875em calc(100% - 48.875em - 4.5em);
    justify-content: space-between;
    align-items: center;
  }
  .text{
    margin-top: 3em;
  }
  &__nav{
    display: flex;
    margin-top: 3em;
  }
  &__img{
    height: 45em;
    img{
      height: 100%;
      width: auto;
      border-radius: 25em .0625em .0625em 25em;
      display: block;
    }
  }
}

