.article{
  padding-bottom: 3em;
  .text{
    margin-top: 3em;
  }
  &__top{
    display: flex;
    align-items: center;
    margin-top: 2em;
    .m-badge{
      height: 1.875em;
      padding: 0 .625em;
      margin-left: 2em;
      span{
        font-size: .9375em;
      }
    }
  }
  &__bottom{
    margin-top: 3em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .shop-card__slider{
    margin-top: 3em;
    &+.text{
      margin-top: 2em;
    }
    .shop-card__img{
      padding-bottom: 39.5%;
    }
  }
}

.share{
  display: flex;
  align-items: center;
  .socials{
    margin-left: 1em;
  }
}

.comments{
  padding: 3em 0 3.5em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__items{
    margin-top: 2.31em;
    @include grid100gap(1.25em);
  }
}

.comment-card{
  padding: 1.875em 1.25em;
  background-color: $gray3;
  border-radius: 0.1875em;
  &__top{
    @include grid100gap(.5em);
  }
  &__wrap,
  &__bottom{
    display: grid;
    grid-template-columns: calc(100% - 15.125em - 1.5625em) 15.125em;
    gap: 1.5625em;
    align-items: flex-end;
  }
  &__bottom{
    margin-top: 1.56em;
  }
  &__info{
    display: flex;
    align-items: center;
  }
  .avatar{
    margin-right: .75em;
  }
  .catalog-item__rating{
    justify-content: flex-end;
  }
}

.comment-stat{
  display: flex;
  justify-content: flex-end;
  align-items: center;
  &__item{
    @include lastM(0 1.25em 0 0);
    display: flex;
    align-items: center;
    cursor: pointer;
    @include sameNumWidth;
    svg{
      width: 1.375em;
      height: auto;
      margin-right: .5em;
    }
    &._like{
      &:hover,
      &._active{
        path{
          stroke: $green;
        }
      }
    }
    &._unlike{
      &:hover,
      &._active{
        path{
          stroke: $primary;
        }
      }
    }
  }
}

.news-collection{
  padding: 3.5em 0 7em;
  @media screen and (min-width: 993px){
    .swiper-slide-next+.swiper-slide+.swiper-slide{
      opacity: 0;
      pointer-events: none;
    }
  }
}

.news-slider{
  margin-top: 2.56em;
  .swiper{
    &-slide{
      &._big{
        width: 50%;
      }
    }
  }
}

.news-card._big,
.news-slider ._big{
  .news-card__title{
    -webkit-line-clamp: 2;
    max-width: 30em;
  }
}

.news-card{
  height: 36.3125em !important;
  border-radius: 0.1875em;
  position: relative;
  color: #fff;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, rgba(31, 33, 35, 0.00) 0%, rgba(31, 33, 35, 0.60) 60.94%, #1F2123 100%);
    z-index: 2;
    border-radius: .1875em;
  }
  &__img{
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: cover;
    position: absolute;
    border-radius: .1875em;
  }
  .m-badge{
    position: absolute;
    z-index: 3;
    top: 2.25em;
    left: 2.5em;
    height: 2.0625em;
    padding: 0 .625em;
    background-color: #fff;
    color: #4B6C8D;
    border-radius: .1875em;
    &._green-text{
      color: #469064;
    }
  }
  &__content{
    position: absolute;
    z-index: 3;
    bottom: 0;
    left: 0;
    padding: 2.5em;
    width: 100%;
    @include grid100gap(1.25em);
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: .34em 0;
  }
  &__title,
  &__desc{
    @include truncate(3);
  }
  &__title{
    max-width: 18em;
  }
}

.blog{
  padding-bottom: 8em;
  &__links{
    margin-top: 2.44em;
    display: flex;
    .m-link{
      @include lastM(0 2.5em 0 0);
    }
  }
  &__grid{
    margin-top: 2.44em;
    grid-auto-flow: dense;
    ._big{
      grid-column: span 2;
    }
  }
}
