.hot-line{
  padding-bottom: 6.75em;
  &__inner{
    padding: 2.5em;
    background-color: $blue5;
    border-radius: .1875em;
    margin-top: 2.94em;
    display: grid;
    grid-template-columns: calc(100% - 10.25em) 7.25em;
    gap: 3em;
    color: #302A73;
  }
  .socials{
    padding: 1.75em;
    border-radius: 5.625em;
    background: #FFF;
    flex-direction: column;
    &__item{
      width: 3.75em;
      @include lastM(0 0 1.625em);
    }
  }
  &__text{
    p{
      font-size: 1.125em;
      line-height: 178%;
    }
    hr{
      height: 2em;
      display: block;
      opacity: 0;
    }
  }
  &__nav{
    margin-top: 3.25em;
  }
}
