.promos{
  padding-bottom: 1.13em;
  &__grid{
    margin-top: 2.31em;
  }
}

.promo-card{
  position: relative;
  color: #fff;
  display: block;
  &__img{
    border-radius: .1875em;
    @include imgCover(47%);
  }
  &__content{
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    width: 19em;
    height: 100%;
    padding: 2.69em 1.94em 2.25em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
  }
  &__logo{
    max-width: 9.8125em;
    max-height: 2.6875em;
    display: block;
    flex-shrink: 0;
  }
}

.categories{
  padding: 4.06em 0 2.37em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.categories-slider{
  margin-top: 2.88em;
  &:first-child{
    margin-top: .2em;
  }
  .swiper{
    &-slide{
      width: calc(100% / 7);
      @media screen and (min-width: 993px){
        &-next+.swiper-slide+.swiper-slide+.swiper-slide,
        &-next+.swiper-slide+.swiper-slide+.swiper-slide+.swiper-slide,
        &-next+.swiper-slide+.swiper-slide+.swiper-slide+.swiper-slide+.swiper-slide,{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
  }
}

.category-card{
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  background-color: $gray3;
  border-radius: .1875em;
  padding: .58em 1em 1.875em;
  text-align: center;
  &:hover{
    background-color: $black;
    color: #fff;
  }
  &__img{
    height: 22.5em;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    margin-bottom: 1.94em;
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
}

.categories2{
  padding: 3.5em 0;
  ._small{
    padding-top: 0;
    padding-bottom: 1.5em;
    .category-card__img{
      height: 12.6875em;
      margin-bottom: .94em;
    }
  }
  ._big{
    grid-row: span 2;
    justify-content: space-between;
  }
}

.brands{
  padding: 3.5em 0 .75em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(6,calc(100% / 6));
    margin-top: 2.81em;
    background-color: $gray3;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 0;
      width: 100%;
      height: 1px;
      background: #EDEDED;
    }
    .brand-card{
      &:before{
        margin-left: 0;
      }
      @media screen and (min-width: 993px){
        &:nth-child(6n){
          &:before{
            display: none;
          }
        }
      }
    }
  }
}

.brands-slider{
  position: relative;
  margin-top: 3.5em;
  &__wrap{
    overflow: hidden;
  }
  .swiper{
    &-container{
      background-color: $gray3;
      width: calc(100% + 1.5em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      padding-right: 1.5em;
      height: auto;
      width: calc(100% / 7);
    }
    &-button{
      top: 50%;
      margin: 0;
      transform: translateY(-50%);
      &-prev{
        left: -3.875em;
      }
      &-next{
        right: -3.875em;
      }
    }
  }
}

.brand-card{
  height: 11.375em;
  @include center;
  padding: 2.84em;
  position: relative;
  &:not(:hover){
    img{
      opacity: .4;
    }
  }
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 100%;
    width: 1px;
    height: 100%;
    background: #EDEDED;
    margin-left: .75em;
  }
  img{
    max-width: 100%;
    max-height: 100%;
    transition: all .3s;
  }
}

.catalog-tags{
  overflow: hidden;
  .swiper{
    &-slide{
      width: auto;
      padding-right: .67em;
    }
  }
}

.tag{
  height: 2.0625em;
  display: flex;
  align-items: center;
  padding: 0 1em;
  border-radius: 0.1875em;
  border: 1px solid #D9D9D9;
  font-weight: 700;
  white-space: nowrap;
  span{
    font-size: .875em;
    line-height: 120%;
  }
  &._active{
    pointer-events: none;
  }
  &._active,
  &:hover{
    background-color: $primary;
    border-color: $primary;
    color: #fff;
  }
}

.catalog-categories{
  padding: 7.32em 0 2.6em;
  &__items{
    @include grid100gap(5.625em);
  }
}

.catalog-group{
  display: grid;
  grid-template-columns: 40.375em calc(100% - 40.375em - 5.19em);
  justify-content: space-between;
  &__img{
    height: 26.875em;
    @include center;
    padding: 5em 4.5em;
    background-color: $blue3;
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
  .main__count{
    color: #AFAFAF;
    border-color: #D7D7D7;
  }
  &__items{
    column-count: 3;
    column-gap: 6.44em;
    margin-top: 2.625em;
  }
  .brand-item{
    @include lastM(0 0 1em);
  }
}

.brands3{
  padding: 5em 0 6.25em;
}

.form-filter{
  display: grid;
  grid-template-columns: repeat(5,calc(20% - 2em));
  gap: 1.15em 2.5em;
}

.form-checkbox{
  height: 2.375em;
  svg{
    height: 100%;
    width: auto;
  }
  input:checked~svg{
    g{
      fill: $primary;
    }
  }
}
