.reviews-collection{
  padding: 4.56em 0 3.56em;
  background-color: $gray3;
  .review-card{
    height: 100%;
  }
}

.reviews-slider{
  margin-top: 3.125em;
}

.history{
  padding: 4.6em 0;
  .text{
    margin-top: 2em;
  }
  &__items{
    margin-top: 3em;
  }
}

.history-card{
  display: grid;
  grid-template-columns: 7.3em calc(100% - 23.6125em - 3.75em) 16.3125em;
  gap: 1.875em;
  align-items: flex-start;
  &:not(:last-child){
    padding-bottom: 2em;
    margin-bottom: 2em;
    border-bottom: 1px solid #EFEFEF;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  .text{
    margin-top: 1em;
  }
  &__year{
    @include center;
    width: 6em;
    height: 2.625em;
    border-radius: 3.125em;
    background-color: $black;
    color: #fff;
    font-weight: 700;
  }
}
