.lk{
  padding-bottom: 5em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 2.5em;
  }
}

.lk-links{
  display: flex;
  align-items: center;
}

.lk-link{
  @include center;
  padding: 0 1.25em;
  height: 2.875em;
  border-radius: 0.1875rem;
  border: 1px solid #D9D9D9;
  font-weight: 700;
  @include lastM(0 .81em 0 0);
  span{
    font-size: .9375em;
    line-height: 120%;
  }
  svg{
    width: 1.125em;
    height: auto;
    margin-right: .56em;
    flex-shrink: 0;
  }
  &._big{
    svg{
      width: 1.375em;
    }
  }
  &._medium{
    svg{
      width: 1.25em;
    }
  }
  &._active,
  &:hover{
    background-color: $primary;
    border-color: $primary;
    color: #fff;
    path:not(._not-stroke){
      stroke: #fff;
    }
    ._fill-hover{
      fill: #fff;
    }
  }
}

.lk-status{
  display: flex;
  align-items: center;
  padding: .875em 1.5em;
  border-radius: 0.1875em;
  background-color: $gray3;
  &__btn{
    margin-left: 1em;
    text-decoration: underline;
    color: $black;
    &:hover{
      text-decoration-color: transparent;
    }
  }
  &__text{
    display: flex;
    .gray-text{
      margin-right: .5em;
    }
  }
}

.lk-user{
  margin-top: 4.25em;
  max-width: 80.1875em;
}

.m-form{
  padding: 2.5em;
  background-color: $gray3;
  border-radius: 0.1875em;
  &__fields{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
    &._gap12{
      grid-template-columns: repeat(2,calc(50% - .375em));
      gap: .75em;
    }
    .file-field{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
  }
  &__group{
    @include grid100gap(1em);
  }
  &__radios{
    max-width: 66.0625em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 10.25em / 3));
    gap: 1.06em 5.125em;
    &._col2{
      max-width: 44em;
      grid-template-columns: repeat(2,calc(50% - 5.125em / 2));
    }
  }
}

._full{
  grid-column: 1/-1;
}

.user-form{
  margin-top: 1.75em;
}

.lk-company{
  margin-top: 5.0625em;
  max-width: 80.1875em;
}

.change-pass{
  margin-top: 4.25em;
  .m-form{
    margin-top: 1.75em;
    max-width: 43.6875em;
    @include grid100gap(1em);
  }
  &__desc{
    padding-bottom: .625em;
  }
  .m-check{
    padding: .625em 0;
  }
}
