.delivery{
  padding: 4.06em 0 5em;
  &__desc{
    margin-bottom: 3em;
  }
}

.delivery-variants{
  margin-bottom: 3em;
  padding: 3.125em 0;
  background-color: $gray3;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__item{
    height: 4em;
    max-width: 12.81em;
    @include center;
    img{
      display: block;
      max-width: 100%;
      max-height: 100%;
    }
  }
  &._payment{
    .delivery-variants__item{
      max-width: 11em;
    }
    ._small{
      height: 2.6875em;
    }
  }
}
