.reviews{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__link{
    svg{
      width: 1.125em;
      height: auto;
      margin-right: .75em;
      flex-shrink: 0;
    }
    &:hover{
      path{
        stroke: #fff;
        fill: transparent;
      }
    }
  }
  &__items{
    margin-top: 3.375em;
    column-count: 4;
    column-gap: 1.875em;
  }
}

.reviews-content{
  padding: 3.125em 0 4.875em;
  background-color: $gray3;
  margin-top: 2.875em;
}

.review-sites{
  display: flex;
  justify-content: space-between;
}

.site-card{
  max-width: 10.375em;
  &__img{
    height: 3.375em;
    display: flex;
    align-items: center;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__rating{
    margin-top: 1.75em;
    display: flex;
  }
}

.rating-block{
  display: flex;
  align-items: center;
  &__num{
    margin-left: .75em;
    display: flex;
    align-items: center;
  }
}

.rating-block{
  &._disabled{
    pointer-events: none;
  }
}

.rating{
  cursor: pointer;
  &__stars{
    width: 8.25em;
    max-width: 100%;
    position: relative;
    img{
      width: 100%;
      display: block;
    }
  }
  &__fill{
    width: 0;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    overflow: hidden;
    img{
      width: 8.25em;
      display: block;
    }
  }
}

.review-card{
  padding: 1.875em 1.25em;
  display: inline-block;
  width: 100%;
  @include lastM(0 0 1.875em);
  border-radius: 0.1875em;
  background: #FFF;
  &__top{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  &__info{
    display: flex;
    align-items: center;
  }
  &__img{
    margin-right: .75em;
  }
  &__content{
    @include grid100gap(.69em);
  }
  &__site{
    display: flex;
    align-items: center;
    &-split{
      font-size: .875em;
      line-height: 121%;
      margin: 0 .71em;
    }
  }
  &__text{
    margin-top: 1.56em;
  }
  &__answer{
    margin-top: 1.56em;
    @include grid100gap(.625em);
  }
}

.avatar{
  width: 3.125em;
  height: 3.125em;
  flex-shrink: 0;
  img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
    border-radius: 50%;
  }
}

.small-rating{
  display: flex;
  align-items: center;
  img{
    width: 1.25em;
    flex-shrink: 0;
    margin-right: .31em;
  }
}

.video-reviews{
  padding: 5.7em 0;
  &__grid{
    margin-top: 4.19em;
  }
}

.review-card2{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__video{
    display: block;
    margin-top: 1em;
    position: relative;
    &:hover{
      .review-card2__play{
        opacity: 1;
      }
    }
  }
  &__play{
    @include centerAbsolute;
    width: 3.75em;
    opacity: .4;
    transition: all .3s;
    z-index: 3;
  }
  &__img{
    border-radius: .1875em;
    @include imgCover(58.8%);
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(0deg, rgba(31, 33, 35, 0.65) 0%, rgba(31, 33, 35, 0.65) 100%);
      z-index: 2;
      border-radius: inherit;
    }
  }
}
