.brands-slider2{
  margin-top: 2.88em;
  .swiper{
    &-container{
      width: calc(100% + 1.875em);
      &:before{
        content: '';
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
        width: 100%;
        height: 1px;
        background: #EDEDED;
      }
    }
    &-slide{
      padding-right: 1.875em;
      width: calc(100% / 6);
    }
  }
  .brand-card{
    height: 13.25em;
    &:before{
      margin-left: .9375em;
    }
  }
}

.all-brands{
  padding: 4em 0 4.94em;
  &__nav{
    display: grid;
    grid-template-columns: 7.25em calc(100% - 7.25em - 1.875em);
    gap: 1.875em;
    margin-top: 2.94em;
  }
  .search-field{
    .m-btn{
      position: absolute;
      top: 0;
      right: 0;
      height: 100%;
    }
  }
}

.brands-letters{
  margin-top: 3.125em;
  @include grid100gap(1em);
  &__row{
    display: grid;
    grid-template-columns: repeat(32,calc(100% / 32 - 1.4em * 31 / 32));
    gap: .5em 1.4em;
  }
}

.letter-item{
  border-radius: 0.1875em;
  width: 2em;
  height: 2.5625em;
  @include center;
  font-weight: 700;
  &:hover{
    background-color: $primary;
    color: #fff;
    span{
      text-decoration-color: transparent;
    }
  }
  &._disabled{
    pointer-events: none;
    color: #D7D7D7;
  }
  span{
    font-size: 1.125em;
    line-height: 120%;
    text-decoration: underline;
  }
}

.brands-catalog{
  padding-bottom: 3em;
}

.brands-group{
  padding-bottom: 4.44em;
}

.brands-top{
  background-color: $gray3;
  &__inner{
    height: 5em;
    display: grid;
    grid-template-columns: 9.125em calc(100% - 9.125em);
    align-items: center;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(9,calc(100% / 9));
    border-left: 1px solid #EDEDED;
  }
  .brand-card{
    padding: .5em 2em;
    height: 5em;
    &:before{
      margin-left: 0;
    }
  }
}

.brands-items{
  margin-top: 3em;
  &__row{
    column-count: 6;
    column-gap: 3.25em;
  }
}

.brand-item{
  display: inline-flex;
  width: 100%;
  @include lastM(0 0 .375em);
  &__link{
    height: 2.25em;
    @include center;
    padding: 0 .625em;
    border-radius: 0.1875em;
    background-color: $gray3;
    &:hover{
      background-color: $primary;
      color: #fff;
      span{
        text-decoration-color: transparent;
      }
    }
    span{
      font-size: 1em;
      line-height: 120%;
      text-decoration: underline;
    }
  }
}
