@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2em;
    height: 3.125em;
    border-radius: 0.1875em;
    font-weight: 700;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1em;
        line-height: 120%;
    }
}

.m-btn-primary{
    color: #fff;
    background-color: $primary;
    &:hover{
        color: #fff;
        background-color: darken($primary,10%);
    }
}

.m-btn-black-outline{
    color: $black;
    border: 1px solid $black;
    &:hover{
        color: #fff;
        background-color: $black;
        path{
            fill: #fff;
        }
    }
}

.m-btn-white-outline{
    color: #fff;
    border: 1px solid #fff;
    &:hover{
        color: $black;
        background-color: #fff;
    }
}

.m-btn-black{
    color: #fff;
    background-color: $black;
    &:hover{
        color: #fff;
        background-color: $primary;
    }
}

.line-btn{
    color: $black;
    text-decoration: underline;
    span{
        font-size: .9375em;
        line-height: 120%;
    }
    &:hover{
        color: $primary;
    }
}
