
.spinner {
  margin: auto;

  width: 50px;
  height: 50px;
  /* border: 1px solid grey; */
}

.spinner line {
  stroke: #fff;
  stroke-width: 3;
  /* stroke-linecap: round; */

  animation: fade 800ms linear infinite;
}

@keyframes fade {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}
