.news-slider,
.vacancies-slider,
.collection-slider,
.reviews-slider,
.categories-slider{
  position: relative;
  .swiper{
    &-container{
      width: calc(100% + 1.875em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      width: 25%;
      padding-right: 1.875em;
      transition: all .3s;
      @media screen and (min-width: 993px){
        opacity: 0;
        pointer-events: none;
        &-active,
        &-next,
        &-next+.swiper-slide,
        &-next+.swiper-slide+.swiper-slide{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
    &-button{
      &-prev{
        left: -3.875em;
      }
      &-next{
        right: -3.875em;
      }
    }
  }
}
