.contacts{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__items{
    margin-top: 3.875em;
    display: flex;
    justify-content: space-between;
  }
  .map{
    margin-top: 3.875em;
  }
}

.city-tabs{
  display: flex;
}

.city-tab{
  padding: 0 1.5625em;
  height: 3.125em;
  background-color: $black;
  color: #6E7378;
  display: flex;
  align-items: center;
  font-weight: 700;
  transition: all .3s;
  cursor: pointer;
  &._active{
    pointer-events: none;
  }
  &._active,
  &:hover{
    color: #fff;
    background-color: $primary;
    path{
      fill: #fff;
    }
  }
  svg{
    width: 1em;
    height: auto;
    margin-right: .5em;
    flex-shrink: 0;
  }
  span{
    font-size: 1.125em;
    line-height: 120%;
  }
  &:first-child{
    border-top-left-radius: .1875em;
    border-bottom-left-radius: .1875em;
  }
  &:last-child{
    border-top-right-radius: .1875em;
    border-bottom-right-radius: .1875em;
  }
}

.contact-item{
  max-width: 16.3125em;
  .text18{
    line-height: 150%;
  }
  &__desc{
    margin-top: .94em;
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 2em;
    flex-shrink: 0;
    @include lastM(0 .875em 0 0);
    img{
      transition: inherit;
      width: 100%;
      display: block;
    }
    &:hover{
      img{
        transform: translateY(-.5em);
      }
    }
  }
}

.map{
  height: 50em;
}

.shops{
  padding: 5em 0;
  &.pt0{
    padding-top: 0;
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__link{
    padding: 0 2em;
  }
  &__grid{
    margin-top: 3.125em;
  }
}

.shop-card{
  &__slider{
    position: relative;
    overflow: hidden;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(180deg, rgba(31, 33, 35, 0.00) 60.8%, #1F2123 100%);
      z-index: 2;
      border-radius: 0.1875em;
      pointer-events: none;
    }
    .swiper{
      &-pagination{
        position: static;
        width: auto;
        &-bullet{
          background-color: rgba(#fff,.5);
          &-active{
            background-color: #fff;
          }
        }
      }
      &-button{
        position: static;
        margin: 0;
        &:hover{
          border-color: #fff;
          background-color: #fff;
        }
      }
    }
  }
  &__img{
    border-radius: 0.1875em;
    @include imgCover(59.1%);
  }
  &__nav{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    padding: 1.81em 1.38em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 3;
  }

  &__content{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  &__info{
    margin-top: 2.38em;
    margin-right: 1em;
    @include grid100gap(.625em);
  }
}
